.demo {
  max-width: 1240px;
  margin: 100px auto 0;
}

.calendar {
  margin: 0 auto 20px;
}

.calendar .wrap {
  padding: 0 10px;
}

.currentYear {
  text-transform: none;
  text-align: center;
  color: #000;
  margin: 0 auto 50px;
  padding: 0;
  font-family: arial;
  font-size: 32px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
}

.controls {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin: 0 0 50px;
  display: flex;
}

.controls .btn {
  text-transform: none;
  text-align: center;
  color: #fff;
  cursor: pointer;
  background-color: #000;
  border: none;
  margin: 0;
  padding: 13px 20px;
  font-size: 18px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
}

.year {
  flex-flow: wrap;
  margin: 0 -10px;
  display: flex;
}

.month {
  width: 25%;
  box-sizing: border-box;
  margin: 0 0 20px;
  padding: 0 10px;
}

.monthName {
  text-transform: none;
  text-align: center;
  color: #000;
  margin: 0 auto 20px;
  padding: 0;
  font-family: arial;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 1;
}

.month .content {
  flex-flow: wrap;
  display: flex;
}

.day {
  width: 14.1667%;
  text-align: center;
  box-sizing: border-box;
  padding: 6px;
}

.dayOfTheWeek .day {
  font-weight: 800;
}

.booked {
  color: #fff;
  background-color: #000;
}

.isLateCheckout {
  color: #fff;
  background-color: #0000;
  position: relative;
  overflow: hidden;
}

.isLateCheckout > span {
  color: #000;
  background: #fff;
  padding: 0 3px;
}

.isLateCheckout > span:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: -1;
  background: linear-gradient(to bottom right, #000 50%, #0000 50%);
  position: absolute;
  top: 0;
  left: 0;
}

.key {
  margin: 0 absolute;
  flex-direction: row;
  display: flex;
}

.key .type {
  margin: 0 10px 0 0;
}

.key .type span {
  line-height: 30px;
  display: flex;
  position: relative;
}

.key .type span:before {
  content: "";
  width: 30px;
  height: 30px;
  margin: 0 10px 0 0;
  display: block;
}

.bookedType span:before {
  background: #000;
}

.lateCheckoutType span:before {
  background: linear-gradient(to bottom right, #000 50%, #0000 50%);
}

@media (max-width: 980px) {
  .month {
    width: 50%;
  }
}

@media (max-width: 768px) {
  .month {
    width: 100%;
  }
}

